@import url(https://fonts.googleapis.com/css2?family=Sarabun:wght@100&display=swap);
* {
  margin: 0;
  padding: 0;
}

:root {
  font-family: "Sarabun", sans-serif;
  --background: #c5bab38e;
  /*--background-darker: #f5f2f18e;*/
  --background-darker: #faf7f58e;
  --line: #5c4e4460;
  --blue: #271ab6;
  --yellow: #db9e1b;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

nav {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  padding: 0 2rem;
  font-size: 1.2rem;
}

nav p {
  font-weight: bold;
}

ol {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  width: 35rem;
  list-style-type: decimal-leading-zero;
}

hr {
  width: 91vw;
  margin: 1rem auto;
  background-color: #5c4e4460;
  background-color: var(--line);
  border: solid 0.01rem #5c4e4460;
  border: solid 0.01rem var(--line);
  opacity: 0.3;
}

.App {
  background-color: #c5bab38e;
  background-color: var(--background);
  padding: 2rem;
}

.intro {
  display: -webkit-flex;
  display: flex;
}

h1 {
  font-size: 4rem;
}

.intro h1 {
  z-index: 1;
  width: 65%;
  padding: 4rem 6rem;
}

.intro span {
  color: #271ab6;
  color: var(--blue);
}

.circleDiv {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  margin: auto;
}

.circle {
  height: 5rem;
  width: 5rem;
  z-index: 1;
  border-radius: 100%;
  margin-top: -3.5rem;
  margin-right: 8rem;
  -webkit-align-self: flex-end;
          align-self: flex-end;
  background-color: #db9e1b;
  background-color: var(--yellow);
}

header {
  max-width: 100vw;
}

.intro .gradient {
  margin-left: -11rem;
  margin-top: 6rem;
  height: 18rem;
  width: 18rem;
}

.skill .gradient {
  height: 5rem;
  width: 5rem;
}

.mySkills div .skill:nth-of-type(3n + 2) .gradient {
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
}

.mySkills div .skill:nth-of-type(3n) .gradient {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}

.gradient {
  border-radius: 100%;
  background-color: #faf7f58e;
  background-color: var(--background-darker);
  background: conic-gradient(#faf7f58e, #c5bab38e);
  background: conic-gradient(var(--background-darker), var(--background));
}

.aboutMe {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  margin: 3rem 0 6rem 2rem;
  width: 82vw;
}

.aboutPics {
  padding-left: 4rem;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
}

.aboutPics img:last-of-type {
  -webkit-align-self: flex-end;
          align-self: flex-end;
  z-index: 1;
  margin-left: -5rem;
  object-fit: cover;
}

.about {
  width: 30vw;
  margin: 5rem 8rem 0 0rem;
}

.about h2 {
  padding-bottom: 2rem;
  font-weight: bold;
}

.about p {
  font-size: 1.2rem;
}

.middle {
  width: 90%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  margin: auto;
  margin-top: 3rem;
}

.middle h1 {
  padding-bottom: 4rem;
}

.projectWrap {
  width: 100%;
  display: -webkit-flex;
  display: flex;
}

.allProjects {
  z-index: 1;
  width: 70%;
  margin: auto;
}

.project span {
  font-size: 1.1rem;
  font-style: italic;
}

.allProjects hr {
  width: 70%;
  margin-right: 0;
  margin-top: 2rem;
}

.projectWrap .gradient {
  height: 18rem;
  width: 18rem;
  margin-top: -8rem;
  margin-right: -20rem;
}

.project {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  width: 100%;
  height: 5rem;
}

.project > p {
  width: 30%;
  -webkit-align-self: center;
          align-self: center;
}

.project div {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-flex: 1 1;
          flex: 1 1;
}

.project i {
  -webkit-align-self: center;
          align-self: center;
}

.cvBox {
  padding: 8rem;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.cvBox > div {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  padding: 2rem;
  width: 20%;
  background-color: white;
  margin-bottom: 4rem;
}

.cvBox button:hover {
  color: black;
  cursor: pointer;
}

.cvBox button:focus {
  outline: none;
}

.cvBox img {
  margin-top: 4rem;
}

.cvBox button {
  border: none;
  border-radius: 2rem;
  width: 10rem;
  height: 3rem;
  color: white;
  background-color: #271ab6;
  background-color: var(--blue);
}

.skill p {
  font-weight: bold;
}

.mySkills {
  width: 70%;
  margin: auto;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.mySkills h1 {
  padding: 3rem 0;
}

.mySkills > div {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  margin-top: 1rem;
}

.skill {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  width: 25%;
  height: 30rem;
}

.skill > h2 {
  font-size: 1.4rem;
  height: 8%;
  padding: 2rem 0;
}

.skill > ul {
  height: 3rem;
  padding: 2rem 0;
  list-style: inside square;
}

.skill > ul > li {
  padding-bottom: 1rem;
}

.contact {
  width: 90%;
  margin: auto;
  margin-top: 5rem;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}

.contact img {
  z-index: 1;
  margin-bottom: -13rem;
}

.contact > div {
  width: 100%;
  height: 37rem;
  background-color: white;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: flex-end;
          align-items: flex-end;
}

.contactInfo {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  height: 50%;
  width: 29%;
  margin-left: auto;
  margin-right: auto;
  padding: 2rem 0;
}

.contactInfo p {
  padding: 0.5rem 0 2rem 0;
}

.contactInfo h1 {
  padding: 3rem 0;
}

.contactInfo hr {
  width: 100%;
}

.contactInfo > div {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  width: 40%;
}

a {
  color: black;
  text-decoration: none;
}

a:hover {
  color: #271ab6;
  color: var(--blue);
}

